.si-title {
  position: relative;
  overflow: hidden;
  height: 300px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: blue;
  font-size: 20px;

  &::before {
    content: attr(data-watermark);
    position: absolute;
    top: -75%;
    left: 0;
    right: 0;
    display: block;
    opacity: 0.7;
    color: red;
    font-size: 50px;
  }
}

// card
.si-card {
  &__wrap {}

  &__row {
    &--bot {}

    &--user {}
  }

  &__profile {}

  &__item {
    ul {
      justify-content: flex-start;
      align-items: flex-start;
      @extend %flexbox !optional;

      li {
        width: 120px;
        height: 180px;
      }
    }
  }

  &__box {}
}